/* eslint-disable react/prop-types */
import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";

import { SERVER_USER_SIGN_UP, SIGN_IN_URL } from "../../common/Url";
import { validateEmail } from "../../common/RegexValidator";
import Footer from "../../components/Footer/Footer";

import {
  Avatar,
  Button,
  CssBaseline,
  TextField,
  Grid,
  Container,
  Typography,
} from "@material-ui/core";
import AssignmentIcon from "@material-ui/icons/Assignment";

import axios from "axios";
import { FormattedMessage, injectIntl } from "react-intl";

class SignUp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      firstname: "",
      lastname: "",
      alertMessage: "",
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  /**
   * Handling the firstname lastname email and password changes
   * @param {*} event
   */
  handleChange(event) {
    const value = event.target.value;
    this.setState({
      ...this.state,
      [event.target.name]: value,
    });
  }

  /**
   *Handling the submit
   * @returns
   */
  handleSubmit() {
    if (!validateEmail(this.state.email)) {
      this.setState({
        alertMessage: <FormattedMessage id="login.validateEmail" />,
      });
      return;
    }

    if (this.state.password.length < 6) {
      this.setState({
        alertMessage: <FormattedMessage id="login.validPassword" />,
      });
      return;
    }

    if (this.state.firstname.length == 0) {
      this.setState({
        alertMessage: <FormattedMessage id="login.validFirstname" />,
      });
      return;
    }

    if (this.state.lastname.length == 0) {
      this.setState({
        alertMessage: <FormattedMessage id="login.validLastname" />,
      });
      return;
    }

    axios
      .post(SERVER_USER_SIGN_UP, {
        email: this.state.email,
        username: this.state.firstname + " " + this.state.lastname,
        password: this.state.password,
      })
      .then((res) => {
        console.log(res);
        this.props.history.push(SIGN_IN_URL);
      })
      .catch((err) => {
        if (err.response) {
          if (err.response.status == 409) {
            this.setState({
              alertMessage: <FormattedMessage id="login.existedEmail" />,
            });
          } else {
            this.setState({
              alertMessage: err.response.data,
            });
          }
        } else {
          this.setState({
            alertMessage: <FormattedMessage id="login.serverError" />,
          });
          console.log(err);
        }
      });
  }

  render() {
    return (
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div
          style={{
            marginTop: 50,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar
            style={{ marginTop: 20, marginBottom: 10, background: "#FFD166" }}
          >
            <AssignmentIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            <FormattedMessage id="landing.signUp" />
          </Typography>
          <form style={{ marginTop: 10, width: "100%" }} noValidate>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <TextField
                  variant="outlined"
                  margin="normal"
                  required
                  id="firstname"
                  label={this.props.intl.formatMessage({
                    id: "login.firstName",
                  })}
                  name="firstname"
                  autoFocus
                  value={this.state.firstname}
                  onChange={this.handleChange}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  variant="outlined"
                  margin="normal"
                  required
                  name="lastname"
                  label={this.props.intl.formatMessage({
                    id: "login.lastName",
                  })}
                  id="lastname"
                  value={this.state.lastname}
                  onChange={this.handleChange}
                />
              </Grid>
            </Grid>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="email"
              label={this.props.intl.formatMessage({
                id: "login.email",
              })}
              id="email"
              value={this.state.email}
              onChange={this.handleChange}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="password"
              onChange={this.handleChange}
              value={this.state.password}
              label={this.props.intl.formatMessage({
                id: "login.password",
              })}
              type="password"
              id="password"
            />

            <Button
              type="button"
              fullWidth
              variant="contained"
              color="primary"
              onClick={this.handleSubmit}
              style={{
                color: "white",
                background: "#354868",
                marginTop: 20,
                marginBottom: 30,
              }}
            >
              <FormattedMessage id="landing.signUp" />
            </Button>
            <Grid container justify="flex-end">
              <Grid item>
                <p style={{ color: "red" }}>{this.state.alertMessage}</p>
              </Grid>
            </Grid>
            <Grid container justify="flex-end">
              <Grid item>
                <Grid
                  component={Link}
                  to={SIGN_IN_URL}
                  variant="body2"
                  style={{
                    color: "#FFD166",
                  }}
                >
                  <FormattedMessage id="login.alreadyHaveAccount" />
                </Grid>
              </Grid>
            </Grid>
          </form>
        </div>
        <Footer />
      </Container>
    );
  }
}

export default withRouter(injectIntl(SignUp));
