/* eslint-disable react/prop-types */
import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";

import {
  SERVER_USER_LOG_IN,
  WORKSPACES_URL,
  SIGN_UP_URL,
} from "../../common/Url";
import { decodeStorageToken } from "../../common/TokenUtil";
import Footer from "../../components/Footer/Footer";

import {
  Avatar,
  Button,
  CssBaseline,
  TextField,
  Grid,
  Container,
  Typography,
} from "@material-ui/core";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";

import axios from "axios";
import { FormattedMessage, injectIntl } from "react-intl";

class SignIn extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      errorMessage: "",
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  /**
   * Handling the email and password changes
   * @param {*} event
   */
  handleChange(event) {
    const value = event.target.value;
    this.setState({
      ...this.state,
      [event.target.name]: value,
    });
  }

  /**
   * Handling the submit
   * @param {*} event
   */
  handleSubmit(event) {
    event.preventDefault();
    axios
      .post(SERVER_USER_LOG_IN, {
        username: this.state.email,
        password: this.state.password,
      })
      .then((res) => {
        if (res.data.access_token) {
          console.log("Token stored");
          localStorage.setItem("token", res.data.access_token);
          // decode the email from token, then set it to localStorage
          decodeStorageToken(res.data.access_token);
          this.props.history.push(WORKSPACES_URL);
        } else {
          this.setState({
            errorMessage: <FormattedMessage id="login.serverError" />,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        this.setState({
          errorMessage: (
            <FormattedMessage id="login.validateEmailandPassword" />
          ),
        });
      });
  }

  render() {
    return (
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div
          style={{
            marginTop: 50,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar
            style={{ marginTop: 20, marginBottom: 10, background: "#FFD166" }}
          >
            <AccountCircleIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            <FormattedMessage id="landing.signIn" />
          </Typography>
          <form style={{ marginTop: 10, width: "100%" }} noValidate>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label={this.props.intl.formatMessage({
                id: "login.email",
              })}
              name="email"
              autoFocus
              value={this.state.email}
              onChange={this.handleChange}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="password"
              label={this.props.intl.formatMessage({
                id: "login.password",
              })}
              type="password"
              id="password"
              value={this.state.password}
              onChange={this.handleChange}
            />

            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              style={{
                color: "white",
                background: "#354868",
                marginTop: 20,
                marginBottom: 30,
              }}
              onClick={this.handleSubmit}
            >
              <FormattedMessage id="landing.signIn" />
            </Button>
            <Grid container justify="flex-end">
              <Grid item>
                <p style={{ color: "red" }}>{this.state.errorMessage}</p>
              </Grid>
            </Grid>
            <Grid container>
              <Grid item>
                <Grid
                  component={Link}
                  to={SIGN_UP_URL}
                  variant="body2"
                  style={{
                    color: "#FFD166",
                  }}
                >
                  <FormattedMessage id="login.dontHaveAccount" />
                </Grid>
              </Grid>
            </Grid>
          </form>
        </div>
        <Footer />
      </Container>
    );
  }
}

export default withRouter(injectIntl(SignIn));
